	// 自定义gameItem组件
	import gameItem from '@/components/k-ui/k-game-list/k-game-participate.vue';
	import {
		mapState
	} from 'vuex';
	export default {
		components: {
			gameItem
		},
		computed: {
			...mapState(['hasLogin', 'userInfo']),
		},
		data() {
			return {
				currentPage: 1,
				datas: [],
				empty: false
			}
		},
		onLoad(options){
			this.getDatas(options.id)
		},
		watch:{
			// 监控数据名为datas的属性,显示empty界面
			datas(e){
				// console.log(e)
				let empty = e.length === 0 ? true : false
				if(this.empty !== empty){
					this.empty = empty
				}
				
			}
		},
		onReachBottom() {
			// if (this.notices.length < this.currentPage * this.$common.pageSize) return;
			// this.currentPage++
			// this.getNotices()
		},
		methods:{
			async getDatas(id) {
				// console.log("gameList: " + this.currentPage)
				const res = await this.$api.request({
					url: '/game/join/' + id,
					method: 'GET',
					data: {
						currentPage: this.currentPage,
						pageSize: this.$common.pageSize
					}
				})
				console.log(res)
				this.datas = res.data
			
		},
		// 跳转到比赛详情
		goGameDetail(e) {
			uni.navigateTo({
				url: '/pages/gameDetail/gameDetail?id=' + e
			})
		}
	}
}
